@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
.ball {
  display: inline-block;
  width: 100%;
  height: 100%;
  margin: 0;
  border-radius: 50%;
  position: relative;
  background: radial-gradient(
    circle at 50% 40%,
    #fcfcfc,
    #efeff1 66%,
    #9b5050 100%
  );
  overflow: hidden;
  -webkit-animation: ballGrow 2s ease-out forwards;
          animation: ballGrow 2s ease-out forwards;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.ball:after {
  content: '';
  position: absolute;
  top: 5%;
  left: 10%;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: radial-gradient(
    circle at 50% 50%,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0.8) 14%,
    rgba(255, 255, 255, 0) 24%
  );
  -webkit-transform: translateX(-80px) translateY(-90px) skewX(-20deg);
  transform: translateX(-80px) translateY(-90px) skewX(-20deg);
}

.stage {
  width: 300px;
  height: 300px;
  display: inline-block;
  margin: 20px;
  -webkit-perspective: 1200px;
  -ms-perspective: 1200px;
  -o-perspective: 1200px;
  perspective: 1200px;
  -webkit-perspective-origin: 50% 50%;
  -ms-perspective-origin: 50% 50%;
  -o-perspective-origin: 50% 50%;
  perspective-origin: 50% 50%;
}

.number {
  position: absolute;
  width: 100%;
  text-align: center;
  line-height: 300px;
  font-size: 90px;
  color: blue;
  -webkit-animation: ballRoll 2s ease-out;
          animation: ballRoll 2s ease-out;
}

.number:after {
  content: attr(data-number);
  position: absolute;
  -webkit-transform: translateX(-75%);
          transform: translateX(-75%);
  opacity: 0;
  animation: numberReveal 0.1s 1.5s reverse forwards;
}

.number:before {
  content: '?';
  margin-left: 9%;
  position: absolute;
  -webkit-transform: translateX(-25%);
          transform: translateX(-25%);
  -webkit-animation: numberReveal 0.1s 1.5s forwards;
          animation: numberReveal 0.1s 1.5s forwards;
}

@-webkit-keyframes ballRoll {
  0%,
  20%,
  50% {
    opacity: 1;
    -webkit-transform: translateY(0) rotateX(0) scale(1);
            transform: translateY(0) rotateX(0) scale(1);
  }
  10%,
  35%,
  75% {
    -webkit-transform: translateY(100%) rotateX(170deg) scale(0.4);
            transform: translateY(100%) rotateX(170deg) scale(0.4);
  }
  11%,
  36%,
  76% {
    -webkit-transform: translateY(-100%) rotateX(-170deg) scale(0.3);
            transform: translateY(-100%) rotateX(-170deg) scale(0.3);
    opacity: 0;
  }
}

@keyframes ballRoll {
  0%,
  20%,
  50% {
    opacity: 1;
    -webkit-transform: translateY(0) rotateX(0) scale(1);
            transform: translateY(0) rotateX(0) scale(1);
  }
  10%,
  35%,
  75% {
    -webkit-transform: translateY(100%) rotateX(170deg) scale(0.4);
            transform: translateY(100%) rotateX(170deg) scale(0.4);
  }
  11%,
  36%,
  76% {
    -webkit-transform: translateY(-100%) rotateX(-170deg) scale(0.3);
            transform: translateY(-100%) rotateX(-170deg) scale(0.3);
    opacity: 0;
  }
}

@-webkit-keyframes ballGrow {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes ballGrow {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes numberReveal {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes numberReveal {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.slick-prev:before,
.slick-next:before {
  color: darkgray;
}

@font-face {
  font-family: 'yangjin';
  src: url('https://cdn.jsdelivr.net/gh/supernovice-lab/font@0.9/yangjin.woff')
    format('woff');
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: yangjin, Noto Sans KR, dotum, sans-serif;
  padding: 0;
  margin: 0;
}
h1,
h2,
h3 {
  font-family: yangjin, dotum, sans-serif;
}
h4,
h5,
h6,
p,
span {
  font-family: Noto Sans KR, dotum, sans-serif;
}

